import React from 'react'
import ReactDOM from 'react-dom'
import MyIcon from '.';
import { render, renderer } from '../../test.setup';

describe('MyIcon', () => {
  it('renders component without crashing', () => {
    const p = document.createElement('p');
    ReactDOM.render(<MyIcon source={"source"} />, p);
    expect(p).toEqual(<p>source</p>).toMatchSnapshot();
  });

});